import { Box, Container, Grid } from "@mui/material";
import React, { useState } from "react";
import CollapsibleTable from "../Components/Table/Table";
import Search from "../Components/Search";
import PrintDialog from "../Components/PrintDialog/PrintDialog";
import PD from "../Components/PrintDialog/Pd2";

function Body() {
const [searchText, setSearch] = useState("");
const [printTag,setPrinttag] = useState(false);
const [visitordata,setVisitordata] = useState();


  function createData(
    name,
    visiting_dept,
    contact_person,
    date,    
    print,
    history
  ) {
    return {
      name,
      visiting_dept,
      contact_person,
      date,      
      print,
      history: history
    };
  } //schema
  const Headers = [
    "Name",
    "Visiting Dept",
    "Contact Person",
    "Date",    
    "Print",
  ];//table headers
  const rows = [
    createData("Swaminathan", "IT", "Vijendran", "05-05-23", "",{address_of_visitor:"Lucastvs,padi",company:"LTVS",phonenumber:"123456789",addons:"2",purpose:"official"}),
    createData("ArunNehru", "IT", "Samsudeen", "05-05-23", "",{address_of_visitor:"Lucastvs,padi",company:"LTVS",phonenumber:"123456789",addons:"2",purpose:"official"}),
    createData("Chelli", "AdvEngg", "Unknown", "05-05-23", "",{address_of_visitor:"Lucastvs,padi",company:"LTVS",phonenumber:"123456789",addons:"2",purpose:"official"}),
    createData("Sowmya", "IT", "Swaminathan", "05-05-23", "",{address_of_visitor:"Lucastvs,padi",company:"LTVS",phonenumber:"123456789",addons:"2",purpose:"official"}),    
    createData("Ganapathy", "IT", "Swaminathan", "05-05-23", "",{address_of_visitor:"Lucastvs,padi",company:"LTVS",phonenumber:"123456789",addons:"2",purpose:"official"}),    
    createData("Vijendran", "HR", "Person1", "05-05-23", "",{address_of_visitor:"Lucastvs,padi",company:"LTVS",phonenumber:"123456789",addons:"2",purpose:"official"}),    
  ];
  const rowheaders = [
    "Phonenumber","Company","AddOns","Purpose","Address of visitor"
  ]


  const searchOnchange = (newvalue) => {
    console.log(newvalue);
    const search = newvalue;
    setSearch(search);
  };

  const filterRows = () => {
    return rows.filter((row) => {
    console.log(row);
      const searchValue = searchText.toLowerCase();
      console.log(searchText.toLowerCase())
      return (
        row.name.toLowerCase().includes(searchValue) 
      );
    });
  };

  const printformdialog = (e)=>{
    console.log("clicked print data..",e);
    setVisitordata(e);
    handleClickOpen();
  }

  //printformdialog
  const handleClickOpen = () => {    
    setPrinttag(true);
  };

  const handleClose = () => {
    setPrinttag(false);
  };

  return (
    <>
      <Box m={4}>
        <Container maxWidth>
          <Grid xs={12} mb={2}>
            <Search soc={searchOnchange} rowdata={rows}/>
          </Grid>
          <CollapsibleTable rowdata={filterRows()} Headers={Headers} rowheaders={rowheaders} printformdialog = {printformdialog} />
        </Container>
        {/* {printTag&&<PrintDialog printTag={printTag} handleClose={handleClose} visitordata={visitordata}/>} */}
        {printTag&&<PD printTag={printTag} handleClose={handleClose} visitordata={visitordata}/>}
      </Box>
    </>
  );
}

export default Body;
